import React from 'react';
import { Query } from "react-apollo";
import gql from "graphql-tag";
import SpaceX from "../components/SpaceX"
// let gqlData = {};

// const GraphqlData = (limit) => {
//     getData();
//     console.log("from GraphqlData-gqlData:");
//     console.log(gqlData);
//     return gqlData;
// }

const GraphqlData = (limit) => {
    return (
        <Query query={gql`
       {
        launches(limit: 3) {
            id
            mission_name
            details
            launch_success
            rocket {
                rocket_name
            }
            links {
              flickr_images
            }  
        }
        }
    `}
        >
            {({ loading, error, data }) => {
                if (loading) return <p>Loading...</p>;
                if (error) return <p>Error : {error}</p>;

                // gqlData = {
                //     data: [...data.launches],
                //     loading,
                //     error
                // };
                // if (loading) return <p>Loading...</p>;
                // if (error) return <p>Error : {error}</p>;
                // console.log(data);
                return data.launches.map((launch, index) => (
                    <div key={index}>
                        <br/> <br /> <br />
                        <SpaceX launch={launch} />
                    </div>
                ));
            }
            }
        </Query>
    );
};

export default GraphqlData;


